import React, { useState, useEffect } from 'react';
import { useLocation, useParams } from "react-router-dom";
import {
  Box,
  Typography,
  Modal,
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Button,
} from '@mui/material';

function UserModal({ isUserModalOpen, handleModalClose, token ,jsonData}) {
  const [roomMembers, setRoomMembers] = useState([]);
  
  const headers = new Headers({
    'Authorization': `Bearer ${token}`,
    'Content-Type': 'application/json',
  });

  // addToBlockList 
  const addToBlockList = (userId) => {
    const apiUrl = window.location.href.startsWith(
      "https://ron-the-rocker.net/"
    )
      ? `https://ron-the-rocker.net/ndrr/api/user/ng-list`
      : `http://localhost:7777/user/ng-list`;

    const data = {
      "blocked_user_id": userId,
    };
    fetch(apiUrl, {
      method: 'POST', //  'DELETE'
      headers: headers,
      body: JSON.stringify(data),
    })
      .then(response => {
        if (response.ok) {
          // 
          console.log(`User ${userId} has been blocked/unblocked.`);
        } else {
          // 
          if (response.status === 401) {
            // 
            console.error('');
          } else if (response.status === 500) {
            // 
            console.error('');
          } else {
            // 
            console.error('API');
          }
        }
      })
      .catch(error => {
        // fetch
        console.error('API:', error);
      });
  };

  // removeFromBlockList 
  const removeFromBlockList = (userId) => {
    const apiUrl = window.location.href.startsWith(
      "https://ron-the-rocker.net/"
    )
      ? `https://ron-the-rocker.net/ndrr/api/user/ng-list`
      : `http://localhost:7777/user/ng-list`;

    const data = {
      "blocked_user_id": userId,
    };

    fetch(apiUrl, {
      method: 'DELETE',
      headers: headers,
      body: JSON.stringify(data),
    })
      .then(response => {
        if (response.ok) {
          // 
          console.log(`User ${userId} has been unblocked.`);
        } else {

          if (response.status === 401) {
            // 
            console.error('');
          } else if (response.status === 500) {
          // 
          console.error('');
        }
      }
      })
      .catch(error => {
        console.error(':', error);
      });
  };

  useEffect(() => {
    // isUserModalOpentrue
    if (isUserModalOpen) {
      // jsonDataroom_members ID
      const membersWithCheckbox = jsonData.room_members.filter(member => !member.ami).map(member => ({
        ...member,
        checked: member.blocked, // blockedtrue
      }));
      setRoomMembers(membersWithCheckbox);
    }
  }, [isUserModalOpen, jsonData]);


  return (
    <Modal open={isUserModalOpen} onClose={handleModalClose}>
      <Box
        sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: '90%', // Set width to 95% of the screen size
          bgcolor: 'background.paper',
          boxShadow: 24,
          p: 4,
          maxHeight: '80vh', // Set maximum height of the modal content
          overflowY: 'auto', // Enable vertical scrolling if content overflows
        }}
      >
        {/* Display the room member data in a table */}
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Avatar</TableCell>
                <TableCell>Username</TableCell>
                <TableCell>Ignore</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {roomMembers.map((member) => (
                <TableRow key={member.user_id}>
                  <TableCell>
                    {member.avatar_url && (
                      <div
                        style={{
                          position: 'relative',
                          display: 'inline-block',
                          width: 50,
                          height: 50,
                        }}
                      >
                        <img
                          src={
                            process.env.NODE_ENV === 'development'
                              ? `http://localhost:7777/static/img/${member.avatar_url}`
                              : `https://ron-the-rocker.net/ndrr/api/static/img/${member.avatar_url}`
                          }
                          alt={`Avatar of ${member.username}`}
                          width="50"
                          height="50"
                          style={{ borderRadius: '15%' }}
                        />
                        {/* Cross symbol */}
                        {member.checked && (
                          <div
                            style={{
                              position: 'absolute',
                              top: 0,
                              left: 0,
                              width: '100%',
                              height: '100%',
                              display: 'flex',
                              justifyContent: 'center',
                              alignItems: 'center',
                              fontSize: '4em', // Increase font size for a larger cross
                              color: 'red',
                            }}
                          >
                            &#10060; {/* Unicode for the cross symbol */}
                          </div>
                        )}
                      </div>
                    )}
                  </TableCell>
                  <TableCell>{member.username}</TableCell>
                  <TableCell>
                    <input
                      type="checkbox"
                      checked={member.checked}
                      onChange={() => {
                        // 
                        const updatedMembers = roomMembers.map((m) => {
                          if (m.user_id === member.user_id) {
                            return { ...m, checked: !m.checked };
                          }
                          return m;
                        });
                        setRoomMembers(updatedMembers);

                        if (member.checked) {
                          // 
                          removeFromBlockList(member.user_id);
                        } else {
                          // 
                          addToBlockList(member.user_id);
                        }
                      }}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

        <Button onClick={handleModalClose}>Close</Button>
      </Box>
    </Modal>
  );
}

export default UserModal;